.feedback {
  padding-top: 30px;
}

.feedback__contacts {
  h3 {
    text-align: end;
    font-weight: bold;
    font-size: 30px;
    margin-bottom: 30px;
  }

  ul {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  li {
    width: 45%;
    border: 2px solid @primary-color;
    border-radius: 20px;
    background-color: #ffffff;
    padding: 30px;
  }

  h4 {
    font-size: 28px;
    font-weight: bold;
  }

  a {
    color: @primary-color;
    font-size: 20px;
  }
}

.feedback__item {
  span {
    display: block;
    width: 36px;
    height: 36px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 15px;
  }

  &--email {
    span {
      background-image: url("../img/form-icons/email.svg");
    }
  }

  &--phone {
    span {
      background-image: url("../img/form-icons/phone.svg");
    }
  }
}

.feedback {
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }

  p {
    text-align: center;
  }

  button {
    display: block;
    margin: 0 auto;
  }
}

.feedback__button {
  height: 48px;
  width: 100%;
  max-width: 296px;
  border: 1px solid @primary-color;
  border-radius: 6px;
  background-color: #ffffff;
  color: @primary-color;
  transition: all 0.3s ease-in-out;
  font-size: 18px;

  &:hover {
    background-color: @primary-color;
    color: #ffffff;
  }
}
