.what-offer {
  &__card {
    // margin-bottom: 25px;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 8px;
    height: 100%;

    .text-content {
      h3 {
        margin-bottom: 15px;
        font-weight: bold;
        font-size: 25px;
      }
      p {
        max-width: 570px;
        font-size: 16px;
        font-weight: 300;
      }
    }

    .image-content {
      height: 100%;
      display: flex;
      justify-content: flex-end;
      align-items: center;

      &.image-content-center {
        justify-content: center;
      }
    }
  }
}
