.main-header {
  position: fixed;
  background-color: #ffffff;
  border-bottom: 1px solid #dadada;
  z-index: 1000;

  a {
    transition: all 0.6s ease-in-out;
  }

  a:hover {
    opacity: 0.6;
  }

  .navbar {
    padding: 20px 0;
    background: none;
  }

  .navbar-nav {
    margin: 0 auto;
  }

  .main-header__sign-in {
    border-radius: 10px;
    padding: 10px 15px;
    font-weight: bold;
    margin-right: 10px;
    color: @primary-color;
    border: 1px solid @primary-color;

    &:hover {
      opacity: 1;
      background-color: @primary-color;
      color: #ffffff;
    }
  }

  .main-header__login {
    border: none;
    border-radius: 6px;
    font-weight: bold;
    color: #ffffff;
    background-color: @primary-color;
    padding: 10px 20px;
    transition: all 0.6s ease-in-out;

    &:hover {
      opacity: 1;
      color: @primary-color;
      border: 1px solid @primary-color;
      background-color: #ffffff;
    }
  }
}

