.form {
  padding: 20px;
  // border: 2px solid @primary-color;
  border-radius: 20px;

  button {
    border-radius: 50rem;
    border: none;
    background-color: @primary-color;
    color: #ffffff;
    font-weight: bold;
    padding: 10px 28px;
    font-size: 16px;
  }
}

.form__field {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;

  label {
    position: relative;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 5px;
    margin-left: 30px;
  }

  input,
  textarea {
    border: none;
    border-bottom: 2px solid #dee2e6;
    // border-radius: 6px;
    font-size: 16px;
    padding: 6px 16px;
    margin-bottom: 5px;

    &:focus-visible {
      outline: none;
      border-bottom: 2px solid @primary-color;
    }
  }
}

.form__comment {
  label {
    margin: 0;
    margin-bottom: 5px;
  }

  textarea {
    padding: 5px;
  }
}

.form__radio {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 20px;
  margin-bottom: 20px;

  p {
    width: 100%;
    font-weight: bold;
  }

  label {
    padding: 6px 10px;
    margin: 0;
    border: 2px solid @primary-color;
    border-radius: 16px;
    background-color: #ffffff;
    color: @primary-color;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 20px;
    }

    &:hover {
      background-color: @primary-color;
      color: #ffffff;
    }
  }
}

.form__title {
  span {
    color: #ff0000;
  }

  &::before {
    content: "";
    position: absolute;
    left: -25px;
    top: 0;
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
    background-size: contain;
  }

  &--fio {
    &::before {
      background-image: url("../img/form-icons/fio.svg");
    }
  }

  &--organization {
    &::before {
      background-image: url("../img/form-icons/organization.svg");
    }
  }

  &--city {
    &::before {
      background-image: url("../img/form-icons/city.svg");
    }
  }

  &--email {
    &::before {
      background-image: url("../img/form-icons/email.svg");
    }
  }

  &--phone {
    &::before {
      background-image: url("../img/form-icons/phone.svg");
    }
  }
}

.modal-body {
  padding: 36px;
}

.modal-content {
  border: none;
}

.modal-header {
  padding-top: 36px;
  border-bottom: none;

  h5 {
    font-size: 24px;
    margin: 0 auto;
  }
}
