.functions {
  background-color: #171e3a;

  h2 {
    color: #ffffff;
  }

  img {
    max-height: 600px;
    justify-self: center;
  }
}

.functions__item {
  position: relative;
  padding-left: 48px;
  padding-bottom: 32px;
  color: #ffffff;
  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 8px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: @primary-color;
  }

  &:not(:last-child) {
    &::before {
      content: "";
      position: absolute;
      top: 10px;
      left: 7px;
      width: 2px;
      height: 100%;
      background-color: @primary-color;
    }
  }

  h3 {
    font-size: 24px;
    font-weight: bold;
  }

  p {
    font-size: 18px;
    display: none;
  }

  a {
    font-size: 18px;
    color: @primary-color;
    display: none;
    transition: all 0.6s ease-in-out;

    &:hover {
      transform: scale(1.05);
    }
  }

  &--active {
    &::after {
      content: "";
      left: -4px;
      width: 24px;
      height: 24px;
      background: linear-gradient(285.34deg, #a4b5ff 24%, #add4ff 72.53%);
    }

    &::before {
      content: "";
      left: 10px;
      height: 100%;
      background: linear-gradient(285.34deg, #a4b5ff 24%, #add4ff 72.53%);
    }

    p {
      display: block;
    }

    a {
      display: block;
    }
  }
}
