.stages {
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 35px;
    margin-bottom: 53px;
    li {
      background-color: #ffffff;
      padding: 40px;
      border-radius: 8px;

      span {
        box-sizing: border-box;
        width: 60px;
        height: 60px;
        display: block;
        margin-bottom: 16px;
        color: #ffffff;
        font-size: 25px;
        font-weight: bold;
        background-color: #316AFF;
        padding: 10px 23px;
        border-radius: 8px;
      }

      h3 {
        font-size: 25px;
        font-weight: bold;
        margin-bottom: 16px;
      }

      p {
        font-size: 16px;
      }
    }
  }
  button {
    max-width: 200px;
    padding: 10px 16px;
    background-color: @primary-color;
    color: #ffffff;
    font-weight: bold;
    transition: all 1s ease-in-out;

    &:hover {
      color: @primary-color;
      background-color: #ffffff;
      border: 1px solid @primary-color;
    }
  }
}
