.about-company {
  span {
    color: @primary-color;
  }

  ul {
    background-color: @background-light;
    padding: 20px;
    border-radius: 16px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}

.about-company__item {
  display: flex;
  flex-direction: row;
  padding: 20px;
  border-radius: 10px;
  background-color: #ffffff;

  h3 {
    font-weight: bold;
    margin-bottom: 10px;
  }

  p {
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 0;
  }

  .pic-container {
    border-radius: 10px;
    display: block;
    width: 80px;
    height: 80px;
    margin-right: 20px;
    padding: 10px;
  }

  span {
    display: block;
    width: 60px;
    height: 60px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }

  &--happy {
    .pic-container {
      background-color: #fff0f0;
    }

    span {
      background-image: url("../img/about-company/happy-users.svg");
    }
  }

  &--projects {
    .pic-container {
      background-color: #fffbe6;
    }

    span {
      background-image: url("../img/about-company/projects.svg");
    }
  }

  &--team {
    .pic-container {
      background-color: #edecff;
    }

    span {
      background-image: url("../img/about-company/team.svg");
    }
  }

  &--satisfaction {
    .pic-container {
      background-color: #e6fafb;
    }

    span {
      background-image: url("../img/about-company/satisfaction.svg");
    }
  }
}
