.price {
  padding-bottom: 0;
  background-color: @background-light-blue;

  p {
    margin: 0;
    color: @text-light;
  }

  ul {
    margin-top: 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  li {
    position: relative;
    padding: 20px 60px 20px 20px;
    background-color: #ffffff;
    border-radius: 16px;
    box-shadow: 0 12px 32px rgb(143 154 210 / 16%);

    h5 {
      font-size: 18px;
      font-weight: bold;
      margin-bottom: 10px;
    }

    .price-value {
      font-size: 30px;
      font-weight: bold;
      margin-right: 5px;
    }

    .rouble {
      font-size: 22px;
      font-weight: bold;
    }

    p {
      margin-top: 10px;
    }

    &::after {
      content: "";
      position: absolute;
      width: 40px;
      height: 40px;
      top: 30%;
      right: 10%;
      background-image: url("../img/price-image.svg");
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

.price-slider {
  margin: 0 auto;
  max-width: 400px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: rgba(183, 195, 247, 0.3);
  border-radius: 16px;

  label {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    cursor: pointer;
    transition: all 0.6s ease-in-out;

    .period {
      padding: 8px;
      width: 100%;
      text-align: center;
      line-height: 32px;
      font-size: 1.2rem;
      color: #ffffff;
    }

    input {
      transition: all 0.6s ease-in-out;
    }

    .profitable {
      display: none;
      position: absolute;
      right: 8px;
      top: 8px;
      padding: 4px 8px;
      background: linear-gradient(119.24deg, #496af8 -77.56%, #a880ff 14.27%, #ffb470 107.43%);
      border-radius: 4px;
      color: #ffffff;
    }

    input:checked + .period {
      border-radius: 16px;
      background-color: @primary-color;
    }

    input:checked + .period + .profitable {
      display: block;
    }
  }
}
