.experience {
  h2 {
    margin-bottom: 20px;
  }
}

.experience__sections {
  display: flex;
  flex-direction: row;
  justify-content: center;
  border-bottom: 2px solid #e4e5e7;

  .experience__section {
    padding-bottom: 15px;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 20px;
    }

    &--active {
      color: @primary-color;
      border-bottom: 5px solid @primary-color;
    }

    &:hover {
      color: @primary-color;
    }
  }

  p {
    display: block;
    margin-bottom: 0;
  }
}

.expirience__cards {
  padding: 40px 0;

  .experience__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    padding-bottom: 20px;

    &--not-active {
      display: none;
    }

    li {
      border-radius: 20px;
      background-color: #f6f8fe;
      padding: 25px;
    }

    span {
      color: @primary-color;
    }

    h3 {
      color: #000000;
      margin: 10px 0 20px;
    }

    .expirience__card-type span {
      padding: 10px;
      background-color: #fff0f0;
      color: #000000;
    }
  }
}
